import httpserver from "../../utils/http.js";
import api from "../../utils/api.js";
import getNowFormatDate from "../../utils/timeFormat.js";
// import { setTimeout } from "timers";
import Config from "../../config/config.js";
import { setTimeout } from "timers";
export default {
  data() {
    return {
      siteCode: "",
      scanCode: '',
      serialNO: '',
      tableData: [], //未打印队列数组
      code: "", //产品序列号
      scan: 0, //已扫数量
      trayNumber: 0, //打托数
      materialCode: "", //物料编码
      materialText: "",
      printmaterialCode: "", //物料编码
      printmaterialText: "",
      dialogTableVisible: false, //历史记录弹框
      dialogVisible: false, //补打印弹框
      currentRow: null,
      printdata: [],
      gridData: [],
      currentPage: 1, //当前页
      currentPage1: 1,
      total1: 0, //总数
      pagesize1: 6, //每页显示数量
      total: 0, //总数
      pagesize: 6, //每页显示数量
      serialPort: "", //扫描器端口
      multipleSelection: [],
      palletBarCode: "", //托序列号
      time: "", //打印时间
      count: "", //装托数
      msg: "", //二维码入参
      imgsrc: "", //二维码文件流
      openDialog: false
    };
  },
  beforeRouteLeave(to, from, next) {
    this.closeCom();
    next();
  },
  created() {
    // this.getServertime();
    this.unPrintProductionQueue();
  },
  mounted() {
    this.openCom();
  },
  methods: {
    clickopenDialog() {
      this.openDialog = true;
    },
    saveProductionRecordAndProductionAssembies() {
      let body = {
        scanCode: this.scanCode,
        serialNo: this.serialNO
      };
      httpserver(api.saveProductionRecordAndProductionAssembies, body).then(
        res => {
          if (res.data.returnCode == "0") {

          }
        }
      );
    },
    //打印
    print() {
      console.log("开始打印");
      let strStyleCSS =
        "<style>" + document.getElementById("style1").innerHTML + "</style>";
      let strBodyHtml =
        strStyleCSS +
        "<div id='print'>" +
        document.getElementById("print").innerHTML +
        "</div>";
      LODOP.PRINT_INIT(""); //初始化LODOP内容
      LODOP.ADD_PRINT_HTML(0, 0, "100%", "100%", strBodyHtml);
      // LODOP.SET_PRINT_STYLEA(0, "HtmWaitMilSecs", 2000); //设置上一项延迟超文本下载1000毫秒
      LODOP.PREVIEW(); //打印预览
      //   LODOP.PRINT(); //直接打印
    },
    //  回车事件
    show: function(ev) {
      let _this = this;
      if (ev.keyCode == 13) {
        this.productionMachiningStatusChange();
      }
    },
    //机加下线
    productionMachiningStatusChange() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        serialNo: this.code
      };
      if (this.code != "") {
        httpserver(api.productionMachiningStatusChange, body).then(res => {
          if (res.data.returnCode == "0") {
            this.$message({
              type: "success",
              message: "报工完成！"
            });
            //获取打托数
            this.trayNumber = res.data.data.trayNumber;
            //获取物料编号
            this.materialCode = res.data.data.materialCode;
            this.materialText = res.data.data.materialText;
            //查询队列
            this.unPrintProductionQueue();
          } else {
            this.trayNumber = 0;
            this.scan = 0;
          }
        });
      } else {
        this.$message({
          type: "warning",
          message: "产品序列号不能为空！"
        });
      }
    },
    getHistoryInfo() {
      this.isHistory = true;
      //防止两个弹框冲突
      if (this.isHistory == true) {
        this.dialogTableVisible = true;
      }
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize1
      };
      httpserver(api.getMachingOfflineHisRecords, body).then(response => {
        console.log(response);
        let resData = response.data.data;
        this.gridData = resData.list;
        this.total1 = resData.toalCount;
      });
    },
    currentChange(val) {
      this.currentPage = val;
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workStationCode: loc.workStationCode,
        pageNo: this.currentPage,
        pageSize: this.pagesize1
      };
      httpserver(api.getMachingOfflineHisRecords, body).then(response => {
        let resData = response.data.data;
        this.gridData = resData.list;
        this.total1 = resData.toalCount;
      });
    },
    //获取服务器时间
    getServertime() {
      httpserver(api.getServertime).then(response => {
        this.time = response.data.data;
      });
    },
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return getNowFormatDate.getNowFormatDate(date);
    },

    //查询未打印队列
    unPrintProductionQueue() {
      let loc = JSON.parse(window.localStorage.getItem("terminal"));
      let body = {
        workCenterCode: loc.workCenterCode,
        workStationCode: loc.workStationCode
      };
      httpserver(api.unPrintProductionQueue, body).then(res => {
        this.tableData = res.data.data;
        if (this.materialCode) {
          console.log(this.tableData);
          let count = 0;
          let printArr = [];
          for (var i = 0; i < this.tableData.length; i++) {
            if (this.materialCode == this.tableData[i].materialCode) {
              console.log(this.tableData[i]);
              printArr.push(this.tableData[i]);
              count++;
            }
          }
          this.scan = count;
          if (this.scan == this.trayNumber) {
            //扫描数量到达打托数，自动打印
            let body = {
              palletQuantity: printArr.length,
              palletizedRecordDtoList: printArr,
              workStationCode: JSON.parse(
                window.localStorage.getItem("terminal")
              ).workStationCode,
              workCenterCode: JSON.parse(
                window.localStorage.getItem("terminal")
              ).workCenterCode
            };
            this.getServertime();
            this.antoprint(body);
          }
        }
      });
    },
    //自动打印
    antoprint(body) {
      httpserver(api.insertPalletizedRecords, body).then(res => {
        if (res.data.returnCode == "0") {
          //信息传给打印页面

          let msg = res.data.data;
          this.siteCode = JSON.parse(
            window.localStorage.getItem("terminal")
          ).siteCode;
          this.palletBarCode = msg;
          this.imgsrc =
            Config.apiBaseUrl +
            "pcs/restful/getQrCode?msg=" +
            msg +
            "&siteCode=" +
            this.siteCode;
          this.count = this.scan;
          this.printmaterialCode = this.materialCode;
          this.printmaterialText = this.materialText;
          this.$nextTick(() => {
            console.log(document.getElementById("print"));
            this.print();
          });
          this.materialCode = "";
          this.scan = 0;
          this.trayNumber = 0;
          this.code = "";
          this.unPrintProductionQueue();
        }
      });
    },

    //未打印队列多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleCurrentChange(val) {
      this.currentRow = val;
    },
    empty() {
      console.log(111);
      this.palletBarCode = "";
      this.printmaterialCode = "";
      this.printmaterialText = "";
      this.count = "";
    },
    print1() {
      let time;
      let promise = new Promise(function(resolve, reject) {
        resolve(httpserver(api.getServertime));
      });
      let _this = this;
      promise.then(function(value) {
        _this.time = value.data.data;
        console.log(_this.time);
        if (_this.currentRow != null) {
          _this.siteCode = JSON.parse(
            window.localStorage.getItem("terminal")
          ).siteCode;
          _this.palletBarCode = _this.currentRow.palletBarCode;
          _this.imgsrc =
            Config.apiBaseUrl +
            "pcs/restful/getQrCode?msg=" +
            _this.palletBarCode +
            "&siteCode=" +
            _this.siteCode;
          _this.count = _this.currentRow.palletQuantity;
          _this.printmaterialCode = _this.currentRow.materialCode;
          _this.printmaterialText = _this.currentRow.materialText;

          _this.$nextTick(() => {
            _this.print();
          });
        } else {
          _this.$message({
            type: "warning",
            message: "请选择需打印托盘码"
          });
        }
      });

      //渲染模板之前清空模板数据
      // this.printArr.forEach(element => {
      //    this.siteCode = JSON.parse(
      //     window.localStorage.getItem("terminal")
      //   ).siteCode;
      //   this.palletBarCode = element.;
      //   this.imgsrc =
      //     Config.apiPcsUrl +
      //     "restful/getQrCode?msg=" +
      //     msg +
      //     "&siteCode=" +
      //     this.siteCode;
      //   this.count = this.scan;
      //   this.printmaterialCode = this.materialCode;
      //   this.printmaterialText = this.materialText;
      //   this.$nextTick(() => {
      //     console.log(document.getElementById("print"));
      //     this.print();
      //   });
      // });
    },

    openCom() {
      try {
        let _this = this;
        let baudRate = JSON.parse(window.localStorage.getItem("serialPort"))
          .baudRate;
        console.log(baudRate);
        let port = new SerialPort(
          JSON.parse(window.localStorage.getItem("serialPort")).port, {
            baudRate: parseInt(baudRate),
            autoOpen: false
          },
          false
        );
        console.log(port);
        let Readline = SerialPort.parsers.Readline;
        let parser = new Readline();
        port.pipe(parser);
        port.open(function(error) {
          if (error) {
            console.log(error);
            _this.$message({
              message: "串口打开失败",
              type: "error"
            });
            return console.log("Error opening port:", error);
          } else {
            _this.$message({
              message: "串口打开成功",
              type: "success"
            });
          }
        });
        parser.on("data", function(data) {
          console.log(data);
          if (data.length <= 2) {
            _this.$message({
              type: "error",
              message: "扫描码错误!"
            });
          } else {
            _this.code = data;
            _this.productionMachiningStatusChange();
          }
        });
        _this.serialPort = port;
      } catch (err) {
        //console.log(err);
      }
    },
    closeCom() {
      try {
        if (this.serialPort.isOpen) {
          let _this = this;
          _this.serialPort.close(function(err) {
            if (err) {
              console.log(err);
              _this.$message({
                message: "串口关闭失败",
                type: "error"
              });
            } else {
              _this.$message({
                message: "串口关闭成功",
                type: "success"
              });
            }
          });
        }
      } catch (err) {
        // console.log(err);
      }
    },
    //      补打印
    replenishPrint() {
      this.dialogVisible = true;
      this.getPalletizedRecords();
    },
    // 补打印查询接口
    getPalletizedRecords() {
      let body = {
        palletBarCode: "",
        serialNo: "",
        pageNo: 1,
        pageSize: this.pagesize
      };
      httpserver(api.getPalletizedRecords, body).then(response => {
        console.log(response);
        this.printdata = response.data.data.palletizedRecords;
        this.total = response.data.data.toalCount;
      });
    },
    currentChange1(val) {
      console.log(val);
      this.currentPage1 = val;

      let body = {
        palletBarCode: "",
        serialNo: "",
        pageNo: this.currentPage1,
        pageSize: this.pagesize
      };
      httpserver(api.getPalletizedRecords, body).then(response => {
        this.printdata = response.data.data.palletizedRecords;
        this.total = response.data.data.toalCount;
      });
    },
    // handleCurrentChange(val) {
    //   let body = {
    //     palletBarCode: this.pallinfo,
    //     pageNo: val,
    //     pageSize: this.pagesize
    //   };
    //   httpserver(api.getPalletizedRecords, body).then(response => {
    //     var resData = response.data.data;
    //     this.palletizedData = resData.palletizedRecords;
    //     this.total = resData.toalCount;
    //   });
    // },
    //打印接口
    insertPalletizedRecords() {
      if (this.multipleSelection.length > 0) {
        //判断选中的工单物料编号是否一样
        let count = 0;
        for (var i = 1; i < this.multipleSelection.length; i++) {
          if (
            this.multipleSelection[0].materialCode !=
            this.multipleSelection[i].materialCode
          ) {
            count++;
          }
        }
        console.log(count);
        if (count > 0) {
          this.$message({
            type: "warning",
            message: "物料编号不同无法打托！"
          });
        } else {
          this.getServertime();
          let body = {
            palletQuantity: this.multipleSelection.length,
            palletizedRecordDtoList: this.multipleSelection,
            workStationCode: JSON.parse(window.localStorage.getItem("terminal"))
              .workStationCode,
            workCenterCode: JSON.parse(window.localStorage.getItem("terminal"))
              .workCenterCode
          };
          httpserver(api.insertPalletizedRecords, body).then(res => {
            console.log(res);

            if ((res.data.returnCode = "0")) {
              //渲染打印模版

              let msg = res.data.data;
              this.siteCode = JSON.parse(
                window.localStorage.getItem("terminal")
              ).siteCode;
              this.palletBarCode = msg;
              this.imgsrc =
                Config.apiBaseUrl +
                "pcs/restful/getQrCode?msg=" +
                msg +
                "&siteCode=" +
                this.siteCode;
              this.count = this.multipleSelection.length;
              this.printmaterialCode = this.multipleSelection[0].materialCode;
              this.printmaterialText = this.multipleSelection[0].materialText;
              this.$nextTick(() => {
                console.log(document.getElementById("print"));
                this.print();
              });
              console.log(this.materialCode);
              this.unPrintProductionQueue();
            }
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "未选择打印内容"
        });
      }
    }
  }
};




